//当前页面对象
var _this;
export default {
	data() {
		return {
			title: 'Hello',
			age:34,
			//2种书写方式
			arr1:[{'name':'张三','age':45},{'name':'李四','age':23}],
			arr2:[{name:'张三',age:45}],
		}
	},
	onReady() {
		//获取当前页面页面对象
		_this=this;
		setTimeout(function(){//3秒钟修改数据
		//修改数据
		_this.title='张三';
		_this.age=45;
		
		//直接获取数据
		console.log(_this.arr1);
		console.log(_this.arr2);
		console.log(_this);
		//获取data数据
		console.log(_this._data);
		//data获取属性
		console.log(_this._data.age+'---'+_this._data.title);
		//获取data数据
		console.log(_this.$data);
		//获取data数据失败
		console.log(_this.data);
		
		},1000*5);
	},
	methods: {

	}
}
